import { createRouter, createWebHistory } from 'vue-router';
//import HomeSection from '../components/HomeSection.vue';
//import RecipeList from '../components/RecipeList.vue';
//import AboutSection from '../components/AboutSection.vue';
import HomeSection from './components/HomeSection.vue';
import RecipeList from './components/RecipeList.vue';
import AboutSection from './components/AboutSection.vue';
const routes = [
  { path: '/',name:'Home' ,component: HomeSection },
  { path: '/recipes', name:'Recipes', component: RecipeList },
  { path: '/about', name:'About', component: AboutSection },
  { path: '/menu', name:'Menu', component: () => import('./components/MenuSection.vue') }, // Example for lazy loading
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
